import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CForm,
  CButton,
  CFormLabel,
  CFormInput,
  CFormFeedback,
  CButtonGroup,
} from "@coreui/react";

import { Authentification } from "../../components/index";

import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";

import { useHistory, useParams } from "react-router-dom";

import Message from "../../components/Message";

import { registerUser, getUsers } from "../../provider/userFetch";

const schemaUser = yup.object().shape({
  firstName: yup.string().required("firstName is required"),
  lastName: yup.string().required("lastName is required"),
  email: yup
    .string()
    .email("email must email valid")
    .required("email is required"),
  password: yup.string().required("password are required"),
});

const AjoutAdministrateur = () => {
  /** State */
  const [message, setMessage] = useState(null);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState(false);

  /** Hook Route */
  let history = useHistory();
  let { id } = useParams();

  /** Form Hook */
  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting, isValid },
    resetField,
    setValue,
  } = useForm({
    resolver: yupResolver(schemaUser),
    mode: "onTouched",
  });

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log("Run once, when the component mounts");

    if (id) {
      async function fetch() {
        const resp = await getUsers(null, id);

        if (resp?.status) {
          setValue("firstName", resp?.data.firstName);
          setValue("lastName", resp?.data.lastName);
          setValue("email", resp?.data.email);
          setValue("password", "");
        } else {
          setMessage(resp?.error);
        }
      }

      fetch();
    }
  }, [id, setValue]);

  const onSubmit = async (data) => {
    let resp = null;
    const body = { ...data, role: "admin" };

    if (isValid) {
      /** reste */
      setSuccess(false);
      setError(false);
      resp = await registerUser(body, id);
    }

    if (resp?.status) {
      /** Reset */
      resetField("firstName");
      resetField("lastName");
      resetField("email");
      resetField("password");

      setSuccess(true);
    } else {
      setError(true);
      setMessage(resp?.error);
    }
  };

  const back = () => {
    history.push(`/administrateurs`);
  };

  /* console.log(
    "errors",
    errors,
    isSubmitted, // true si après l'envoi du formulaire
    isSubmitSuccessful, // true si formulaire a été soumis avec succès sans aucun rejet ou a été lancé dans le rappel
    isSubmitting, // true si formulaire est en cours de soumission
    isValid // true si formulaire ne contient aucune erreur
  ); */

  return (
    <CRow>
      <Authentification />
      {error && <Message> {message}</Message>}
      {success && <Message> {`Success Ajout ou Edite`}</Message>}
      <CCol xs={12}>
        <CCard className="mb-4">
          <CCardHeader>
            <CRow>
              <CCol sm={5}>
                <strong>Ajout Administrateur</strong>
              </CCol>
              <CCol sm={7}>
                <CButtonGroup className="float-end me-3">
                  <CButton
                    color="outline-secondary"
                    className="mx-0"
                    onClick={() => back()}
                  >
                    Retour
                  </CButton>
                </CButtonGroup>
              </CCol>
            </CRow>
          </CCardHeader>
          <CCardBody>
            <CForm
              className={`row g-3 needs-validation ${
                isValid && "was-validated"
              }`}
              onSubmit={handleSubmit(onSubmit)}
            >
              <CCol md={6}>
                <CFormLabel htmlFor="firstName">firstName</CFormLabel>
                <CFormInput
                  type="text"
                  id="firstName"
                  required
                  {...register("firstName")}
                />
                <CFormFeedback invalid>
                  {errors.firstName?.message}
                </CFormFeedback>
              </CCol>
              <CCol md={6}>
                <CFormLabel htmlFor="lastName">lastName</CFormLabel>
                <CFormInput
                  type="text"
                  id="lastName"
                  required
                  {...register("lastName")}
                />
                <CFormFeedback invalid>
                  {errors.lastName?.message}
                </CFormFeedback>
              </CCol>
              <CCol md={6}>
                <CFormLabel htmlFor="email">email</CFormLabel>
                <CFormInput
                  type="email"
                  id="email"
                  required
                  {...register("email")}
                />
                <CFormFeedback invalid>{errors.email?.message}</CFormFeedback>
              </CCol>
              <CCol md={6}>
                <CFormLabel htmlFor="password">paswword</CFormLabel>
                <CFormInput
                  type="text"
                  id="password"
                  required
                  {...register("password")}
                />
                <CFormFeedback invalid>
                  {errors.paswword?.message}
                </CFormFeedback>
              </CCol>
              <CCol xs={12}>
                <CButton
                  color="primary"
                  type="submit"
                  className={isSubmitting ? "disabled" : " "}
                >
                  Submit
                </CButton>
              </CCol>
            </CForm>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  );
};

export default AjoutAdministrateur;
